import sitecfg from '@/config.js'
import index from '@/components/index.vue'
import error from '@/components/error.vue'
import fun from "@/components/test.vue"
import axios from 'axios'
const crypto = require('crypto-js')

export default [
    // {
    //     path:'/funselect/',
    //     component:fun
    // },
    {
        path: '/binder/',
        component: index
    },
    {
        path: '/error/',
        component: error,
    },
    {
        path: '/',
        redirect: async function (route, resolve, reject) {
            //不管是访问下次路径还是什么其他路径 ，只要是该主域名下的路径都会导引到该函数内
            //加密解密配置
            //clientURL:前端页面访问根域名,生产模式下,前端页面以public路径存放前端。
            //serverURL:服务端数据提供端访问根域名，一般情况下clientURL与serverURL在生产模式下是一样的.只有在开发模式下才有可能设置成不一样的根域名
            //valiwechatcodePATH:指的是服务器端判断微信公众号用户授权获取openid及判断用户是否为绑定用户的逻辑处理路径。（一般不改变）
            //valitoken：指的是服务器端用来判断前端传来的token是否有效的逻辑处理路径 。（一般不改变）
            //binerokPATH:指的是用户绑定成功后或者是已绑定用户时要去的最终前端页面的路径（生产模式下，一般为public下的前端页面）
            // var sitecfg = {
            //     clientURL: "http://mxthink.cross.echosite.cn",
            //     serverURL: "http://mxthink.cross.echosite.cn",
            //     testURL:"http://mxthinktwo.cross.echosite.cn",
            //     valiwechatcodePATH: '/wechatforsvr/binder/',
            //     valitoken: '/wechatforsvr/valitoken/',
            //     binderokPATH: '/graduateinput/',
            //     wxdebug: false,
            //     sitedebug:false,
            // };
            // let xx = crypto.AES.encrypt(JSON.stringify(sitecfg), 'simple')

            // let debyte = crypto.AES.decrypt(xx, 'simple')
            // console.log(xx + "<===>" + JSON.parse(debyte.toString(crypto.enc.Utf8)))

            // return

            /*判断是否是微信客户端*/
            var ua = navigator.userAgent.toLowerCase();
            var isWeixin = ua.indexOf('micromessenger') != -1;
            var isAndroid = ua.indexOf('android') != -1;
            var isIos = (ua.indexOf('iphone') != -1) || (ua.indexOf('ipad') != -1);
            if (!isWeixin) {
                resolve('/error/')
                return
            }
            /*判断是否是微信客户端 end*/
            
            if (route.query.code) {
                //如果用户是点击微信客户端进来的
                // alert("如果用户是点击微信客户端进来的")
                // alert(route.query.code)
                // alert(sitecfg.serverURL+sitecfg.valiwechatcodePATH)
                window.location = sitecfg.serverURL + sitecfg.valiwechatcodePATH + '?code=' + route.query.code;
                return;
            } else if (route.query.token) {
                //用户经过后台逻辑验证后带着token过来的
                //先验证token的合法性，以headers的Authorization的参数传递给服务端
                // alert("用户经过后台逻辑验证后带着token过来的")
                try {
                    let res = await axios.post(
                        sitecfg.serverURL + sitecfg.valitoken,
                        null,
                        {
                            headers: {
                                Authorization: route.query.token
                            }
                        }
                    );
                    if (res.data.vali) {
                        //合法
                        // alert("合法")
                        sessionStorage.setItem("token", route.query.token)
                        if (res.data.isbinder){
                            if(sitecfg.sitedebug){
                                window.location=sitecfg.testURL+sitecfg.binderokPATH+"?token="+route.query.token
                            }else{
                                window.location=sitecfg.clientURL+sitecfg.binderokPATH+"?token="+route.query.token
                            }
                        }
                        else{
                            // alert("绑定")
                            resolve('/binder/')
                        }
                    } else //不合法
                    {
                        // alert("不合法")
                        resolve("/error/")
                    }
                } catch (err) {
                    alert(err)
                }
                // alert("return了")
                return
            } else {
                // alert("error")
                resolve("/error/")
                return
            }
        }
    }
]


